.form__labelText {
    width: 100%;
    max-width: 200px;
    display: inline-block;
    margin-right: 5px;
}

.form__field {
    border: 1px solid #ccc;
    padding: 5px;
    max-width: 400px;
    width: 100%;
    border-radius: 5px;
    margin: 5px;
}

.form__button {
    width: 50%;
    border: none;
    background-color: hsl(220, 100%, 85%);
    border-radius: 5px;
    padding: 10px;
    margin-top: 10px;
}

.form__button:hover {
    background-color: hsl(220, 100%, 82%);
}

.form__button:active {
    background-color: hsl(220, 100%, 78%);
}

.form__fieldset {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 5px;
    margin: 20px 0;
    background-color: hsl(220, 100%, 95%);
}

.form__legend {
    background-color: hsl(220, 100%, 85%);
    border-radius: 5px;
    padding: 10px;
}